import React, { useEffect, useRef } from 'react';
import { MapContainer, TileLayer, useMap } from "react-leaflet";

const isMobile = () => {
  return window.innerWidth <= 768; // Define mobile para telas menores que 768px
};

// Componente para gerenciar o redimensionamento do mapa
const MapResizeHandler = () => {
  const map = useMap();
  
  useEffect(() => {
    const handleResize = () => {
      // Usa requestAnimationFrame para garantir que o redimensionamento aconteça após a renderização
      requestAnimationFrame(() => {
        map.invalidateSize();
      });
    };

    window.addEventListener('resize', handleResize);
    return () => window.removeEventListener('resize', handleResize);
  }, [map]);

  return null;
};

const MapaBase = ({ children }) => {
  const mapRef = useRef(null);
  
  // Define coordenadas e zoom baseados no dispositivo para visualizar SP
  const defaultPosition = isMobile() ? [-23.5505, -48.6333] : [-23.305, -43.9];
  const defaultZoom = isMobile() ? 6 : 7; // Zoom mais aberto para visualizar mais do estado

  // Detecta se o dispositivo tem tela retina
  const isRetina = window.devicePixelRatio > 1;

  return (
    <div className="h-screen w-screen overflow-hidden">
      <MapContainer
        ref={mapRef}
        center={defaultPosition}
        zoom={defaultZoom}
        className="h-full w-full"
        attributionControl={false}
        zoomSnap={0.25}
        zoomDelta={0.25}
        updateWhenIdle={true}
        updateWhenZooming={false}
        preferCanvas={true}
        maxZoom={18}
        minZoom={4}
        // Configurações adicionais para melhor renderização
        wheelDebounceTime={40}
        wheelPxPerZoomLevel={60}
        zoomAnimation={true}
        fadeAnimation={true}
        markerZoomAnimation={true}
      >
        {/* TileLayer com o mapa de satélite ESRI otimizado */}
        <TileLayer
          url="https://server.arcgisonline.com/ArcGIS/rest/services/World_Imagery/MapServer/tile/{z}/{y}/{x}"
          detectRetina={true}
          // Configurações otimizadas para evitar gaps
          tileSize={isRetina ? 512 : 256}
          zoomOffset={isRetina ? -1 : 0}
          // Buffer e cache otimizados
          keepBuffer={isRetina ? 4 : 2}
          edgeBufferTiles={isRetina ? 3 : 2}
          // Performance e qualidade
          updateWhenIdle={true}
          updateWhenZooming={false}
          updateInterval={150}
          // Limites de zoom
          maxNativeZoom={19}
          maxZoom={19}
          // Otimizações de renderização
          crossOrigin={true}
          bounds={undefined}
          noWrap={true}
          // Prevenção de gaps
          className="leaflet-tile-pane"
          style={{
            imageRendering: isRetina ? 'crisp-edges' : 'auto'
          }}
          attribution='&copy; <a href="https://www.esri.com/">Esri</a> &mdash; Source: Esri, i-cubed, USDA, USGS, AEX, GeoEye, Getmapping, Aerogrid, IGN, IGP, UPR-EGP, and the GIS User Community'
        />
        
        {/* Handler para gerenciar redimensionamento */}
        <MapResizeHandler />

        {/* Conteúdo adicional do mapa (marcadores, camadas, etc.) */}
        {children}
      </MapContainer>
    </div>
  );
};

export default MapaBase;